body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.wrapper {
    height: 100vh;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: column;
            flex-flow: column;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-align-items: center;
            align-items: center;
    padding: 10%;
    text-align: center;
    color: white;
    box-sizing: border-box;

    background: rgba(255,93,177,1);
    background: -webkit-gradient(left top, right bottom, color-stop(0%, rgba(255,93,177,1)), color-stop(18%, rgba(225,82,230,1)), color-stop(23%, rgba(217,79,245,1)), color-stop(53%, rgba(191,98,237,1)), color-stop(76%, rgba(208,0,240,1)), color-stop(90%, rgba(217,5,250,1)));
    background: linear-gradient(135deg, rgba(255,93,177,1) 0%, rgba(225,82,230,1) 18%, rgba(217,79,245,1) 23%, rgba(191,98,237,1) 53%, rgba(208,0,240,1) 76%, rgba(217,5,250,1) 90%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ff5db1', endColorstr='#d905fa', GradientType=1 );
}

h2 {
    margin: 0;
}
p {
    margin: 0;
}
form {
    margin: 20px 5px;
}
input {
    padding: 10px;
    border: 2px solid #1f59b7;
    width: 40vw;
    border-top-left-radius: 12px;
    border-bottom-left-radius: 12px;
    max-width: 300px;
    box-sizing: border-box;
    outline: none;
}
input:focus {
    box-shadow: 0 0 5px 1px rgba(0, 0, 0, .5);
}
input:focus + button{
    box-shadow: 0 0 5px 1px rgba(0, 0, 0, .5);
}
button {
    width: 34vw;
    max-width: 150px;
    text-transform: uppercase;
    border: 2px solid #1f59b7;
    background: #1f59b7;
    font-weight: bold;
    border-top-right-radius: 12px;
    border-bottom-right-radius: 12px;
    color: white;
    outline: none;
    cursor: pointer;
    padding: 10px;
}
.result {
    font-size: 24px;
    text-align: left;
    margin: 0 5px;
}
.result__item {
    margin-bottom: 10px;
}
.error {
    -webkit-transform: translateY(-20px);
            transform: translateY(-20px);
    font-size: 22px;
}
